<template>
  <div :class="computedClass" :style="computedStyle">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'as-col',
  props: {
    col: {
      type: Number,
      default: 1,
      validator(arg) {
        return arg === parseInt(arg) && arg >= 1 && arg <= 12;
      }
    },
    offset: Number,
    xs: [Number, Object],
    sm: [Number, Object],
    md: [Number, Object],
    lg: [Number, Object]
  },
  computed: {
    computedStyle() {
      let styles = {};
      let parent = this.$parent;
      while (parent && parent.$options.name !== 'as-row') {
        parent = parent.$parent;
      }
      if (parent) {
        styles.paddingLeft = parent.offset / 2 + 'px';
        styles.paddingRight = styles.paddingLeft;
      }
      return styles;
    },
    computedClass() {
      let classList = [];
      const {col, offset} = this;

      ['xs', 'sm', 'md', 'lg'].forEach(size => {
        if (typeof this[size] === 'number') {
          classList.push(`as-col-${size}-${this[size]}`);
        } else if (typeof this[size] === 'object') {
          let props = this[size];
          Object.keys(props).forEach(prop => {
            classList.push(
              prop !== 'col'
                ? `as-col-${size}-${prop}-${props[prop]}`
                : `as-col-${size}-${props[prop]}`
            );
          });
        }
      });
      return [
        'as-col-' + col,
        offset ? 'as-col-offset-' + offset : ''
      ].concat(classList);
    }
  }
};
</script>



