import mcopy from 'modern-copy';

export default {
  bind: function(el, binding) {
    if (binding.arg === 'callback') {
      el.$callback = binding.value;
    } else {
      el.$value = binding.value;
      const handler = () => {
        mcopy(el.$value);
        if (el.$callback) {
          el.$callback(el.$value);
        }
      };

      el.addEventListener('click', handler);
      el.$destroy = () => el.removeEventListener('click', handler);
    }
  },
  unbind(el) {
    el.$destroy();
  },
  componentUpdate(el, binding) {
    if (binding.arg === 'callback') {
      el.$callback = binding.value;
    } else {
      el.$value = binding.value;
    }
  }
};



