<template>
  <div :class="computedClass" @mouseout="hover=false" @mouseover="hover=true">
    <input class="as-select__input" :placeholder="placeholder" ref="selectInput" :readonly="!searchable || disabled" @click.stop @blur="handleBlur" @keyup="handleKeyUp" @focus="handleFocus" type="text">
    <div class="as-select__icon" v-show="!computedVisible">
      <i translate="no" class="as-icon icon-arrowdown select-arrow"></i>
    </div>
    <div class="as-select__close" v-show="computedVisible" @click="handleClear">
      <i class="as-icon icon-closed"></i>
    </div>
    <transition name="select-fade">
      <div v-show="active && !disabled" ref="selectOptions" :style="position" class="as-select__dropdown" :class="[{['as-select__dropdown--' + type]: type, 'as-select__dropdown--scroll': scroll, 'as-select__dropdown--disabled': this.disabled}]">
        <ul ref="selectDropdown">
          <slot></slot>
        </ul>
        <ul v-show="empty">
          <li @click="filterItem(''),changeValue()">{{emptyText}}</li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'as-select',
  provide() {
    return {
      'select': this
    };
  },
  props: {
    emptyText: {
      type: String,
      default: 'there is no data'
    },
    placeholder: {
      type: String,
      default: 'Select Item'
    },
    value: {},
    max: [Number, String],
    searchable: Boolean,
    clearable: Boolean,
    type: String,
    multiple: Boolean,
    disabled: Boolean
  },
  data() {
    return {
      active: false,
      valuex: '',
      empty: false,
      scroll: false,
      filterx: false,
      hover: false,
      position: {}
    };
  },
  computed: {
    computedClass() {
      return [
        'as-select',
        {
          'as-select--searchable': this.searchable,
          'as-select--checked': this.active,
          'as-select--disabled': this.disabled,
          ['as-select--' + this.type]: this.type
        }
      ];
    },
    computedVisible() {
      return this.value !== '' && this.clearable && this.hover;
    }
  },
  watch: {
    value(val) {
      this.$emit('change', val);
    },
    active() {
      if (this.active) {
        setTimeout(() => {
          if (this.$refs.selectDropdown.scrollHeight >= 260) {
            this.scroll = true;
          }
        }, 100);
      }
    }
  },
  methods: {
    handleClear(event) {
      this.valuex = '';
      this.$emit('input', '');
      this.$refs.selectInput.value = '';
    },
    handleKeyUp(event) {
      if (this.disabled) return;
      if (this.searchable) {
        this.filterItem(event.target.value);
      }
      this.$children.map(child => {
        child.valueInput = event.target.value;
      });
    },
    handleBlur(event) {
      if (this.searchable && event.relatedTarget ? !event.relatedTarget.closest('.as-select__dropdown') : false) {
        this.closeOptions();
      }
      this.$emit('blur', event);
    },
    handleFocus(event) {
      if (this.disabled) return;
      this.$emit('focus', event);
      this.active = true;

      let selectInput = this.$refs.selectInput;
      setTimeout(() => {
        document.addEventListener('click', this.clickBlur);
      }, 100);
      if (this.searchable && this.multiple) {
        setTimeout(() => {
          if (selectInput.value) {
            selectInput.value = selectInput.value += ',';
          }
          selectInput.selectionStart = selectInput.selectionEnd = 10000;
        }, 10);
      } else if (this.searchable && !this.multiple) {
        selectInput.select();
      }

      this.$nextTick(() => {
        this.position = this.changePosition();
      });
    },
    clickBlur(event) {
      const target = event.target.closest('.as-select__dropdown');
      if (!target) {
        this.closeOptions();
        if (this.searchable) {
          this.filterItem('');
        }
        this.changeValue();
      }
    },
    closeOptions() {
      this.active = false;
      document.removeEventListener('click', this.clickBlur);
    },
    changePosition() {
      const selectInput = this.$refs.selectInput;
      const selectOptions = this.$refs.selectOptions;
      let top = 0;
      let left = 0;
      let width = 0;
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop;

      if (selectInput.getBoundingClientRect().top + selectOptions.clientHeight + 20 > window.innerHeight) {
        top = (selectInput.getBoundingClientRect().top + selectOptions.clientHeight) + scrollTop - selectOptions.scrollHeight;
      } else {
        top = selectInput.getBoundingClientRect().top + scrollTop;
      }
      left = selectInput.getBoundingClientRect().left;
      width = selectInput.offsetWidth;

      return {
        left: `${left}px`,
        top: `${top + 36}px`,
        width: `${width}px`
      };
    },
    addMultiple(value) {
      let currentValue = this.value ? this.value : [];
      if (currentValue.includes(value)) {
        currentValue.splice(currentValue.indexOf(value), 1);
        this.$emit('input', currentValue);
        this.changeValue();
        if (this.searchable) {
          this.$refs.selectInput.focus();
        }
      } else {
        currentValue.push(value);
        this.$emit('input', currentValue);
        this.changeValue();
        if (this.searchable) {
          this.filterItem('');
          this.$refs.selectInput.focus();
        }
      }
    },
    filterItem(value) {
      if (value) {
        this.filterx = true;
      } else {
        this.filterx = false;
      }

      let items = this.$children;
      items.forEach(item => {
        if (item.$children.length > 0) {
          items = [...items, ...item.$children];
        }
      });

      items.map(item => {
        // if not option
        if (!('text' in item)) return;

        let text = item.text;
        if (this.multiple) {
          let valuex = value.split(',');
          valuex.forEach(_value => {
            if (text.toUpperCase().indexOf(_value.toUpperCase()) === -1) {
              item.visible = false;
            } else {
              item.visible = true;
            }
          });
        } else {
          if (text.toUpperCase().indexOf(value.toUpperCase()) === -1) {
            item.visible = false;
          } else {
            item.visible = true;
          }
        }
      });

      const filteredItems = items.filter(item => item.visible);
      if (filteredItems.length === 0) {
        this.empty = true;
      } else {
        this.empty = false;
      }

      this.$nextTick(() => {
        this.position = this.changePosition();
      });
    },
    changeValue() {
      if (this.multiple) {
        let values = this.value ? this.value : [];
        let options = this.$children;

        options.forEach(option => {
          if (option.$children.length > 0) {
            options = [...options, ...option.$children];
          }
        });

        let optionValues = [];
        values.forEach(value => {
          options.forEach(option => {
            if (option.value === value) {
              optionValues.push(option.text.replace('check_circle', '').trim());
            }
          });
        });
        this.$refs.selectInput.value = optionValues.toString();
      } else {
        if (this.$refs.selectInput) {
          this.$refs.selectInput.value = this.valuex;
        }
      }
    }
  },
  mounted() {
    this.changeValue();
    document.body.insertBefore(this.$refs.selectOptions, document.body.firstChild);
  },
  updated() {
    if (!this.active) {
      this.changeValue();
    }
  }
};
</script>



