let hiddenTextArea;

const HIDDEN_STYLE = `
  height: 0 !important;
  visibility:hidden !important;
  overflow: hidden !important;
  position: absolute !important;
  z-index: -1000 !important;
  top: 0 !important;
  right: 0 !important;
`;

const STYLES = [
  'letter-spacing',
  'line-height',
  'padding-top',
  'padding-bottom',
  'padding-left',
  'padding-right',
  'font-family',
  'font-weight',
  'font-size',
  'text-rendering',
  'text-transform',
  'width',
  'text-indent',
  'border-width',
  'box-sizing'
];

export default function calcTextareaHeight(element, minRow = 1, maxRow = null) {
  if (!hiddenTextArea) {
    hiddenTextArea = document.createElement('textarea');
    document.body.appendChild(hiddenTextArea);
  }

  const style = window.getComputedStyle(element);
  const boxSizing = style.getPropertyValue('box-sizing');
  const paddingSize = parseFloat(style.getPropertyValue('padding-bottom')) + parseFloat(style.getPropertyValue('padding-top'));
  const borderSize = parseFloat(style.getPropertyValue('border-bottom-width')) + parseFloat(style.getPropertyValue('border-top-width'));
  const contextStyle = STYLES.map(name => `${name}: ${style.getPropertyValue(name)}`).join(';');

  hiddenTextArea.setAttribute('style', `${contextStyle};${HIDDEN_STYLE}`);
  hiddenTextArea.value = element.value || element.placeholder || '';

  let height = hiddenTextArea.scrollHeight;
  const result = {};

  if (boxSizing === 'border-box') {
    height += borderSize;
  } else if (boxSizing === 'content-box') {
    height -= paddingSize;
  }

  hiddenTextArea.value = '';
  let singleRowHeight = hiddenTextArea.scrollHeight - paddingSize;

  if (minRow !== null) {
    let minHeight = singleRowHeight * minRow;
    if (boxSizing === 'border-box') {
      minHeight += paddingSize + borderSize;
    }
    height = Math.max(minHeight, height);
    result.minHeight = `${minHeight}px`;
  }
  if (maxRow !== null) {
    let maxHeight = singleRowHeight * maxRow;
    if (boxSizing === 'border-box') {
      maxHeight += paddingSize + borderSize;
    }
    height = Math.min(maxHeight, height);
  }

  result.height = `${height}px`;
  hiddenTextArea.parentNode && hiddenTextArea.parentNode.removeChild(hiddenTextArea);
  hiddenTextArea = null;
  return result;
};



