<template>
  <label :class="[
    'as-radio',
    (border || button) ? 'as-radio--' + computedSize : '',
    {
      'as-radio--disabled': comptedDisabled,
      'as-radio--checked': model === label,
      'as-radio--bordered': border,
      'as-radio--button': button
    }]"
    role="radio" :aria-checked="model === label" :aria-disabled="comptedDisabled">
    <span class="as-radio__input">
      <span class="as-radio__inner"></span>
      <input type="radio" class="as-radio__original" aria-hidden="hidden" :value="label" v-model="model" @focus="focus = true" :blur="focus = false" @change="handleChange" :disabled="comptedDisabled" :name="name" tabindex="-1">
    </span>
    <span class="as-radio__label" @keydown.stop>
      <slot></slot>
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>

<script>
import Emitter from '../../utils/emitter';

export default {
  name: 'as-radio',
  inject: {
    asForm: {
      default: ''
    },
    asFormItem: {
      default: ''
    }
  },
  props: {
    value: {},
    label: {},
    disabled: Boolean,
    name: String,
    border: Boolean,
    button: Boolean,
    size: String
  },
  data () {
    return {
      focus: false
    };
  },
  mixins: [Emitter],
  computed: {
    comptedDisabled() {
      return this.isGroup
        ? this._radioGroup.disabled || this.disabled || (this.asForm || {}).disabled
        : this.disabled || (this.asForm || {}).disabled;
    },
    computedSize() {
      const temp = this.size || (this.asFormItem || {}).computedSize;
      return this.isGroup
        ? this._radioGroup.computedSize || temp
        : this.size || temp;
    },
    isGroup() {
      let parent = this.$parent;
      while (parent) {
        if (parent.$options.name !== 'as-radio-group') {
          parent = parent.$parent;
        } else {
          /* eslint-disable */
          this._radioGroup = parent;
          return true;
        }
      }
      return false;
    },
    model: {
      get() {
        return this.isGroup ? this._radioGroup.value : this.value;
      },
      set(val) {
        if (this.isGroup) {
          this.dispatch('as-radio-group', 'input', [val]);
        } else {
          this.$emit('input', val);
        }
      }
    }
  },
  methods: {
    handleChange() {
      this.$nextTick(() => {
        this.$emit('change', this.model);
        this.isGroup && this.dispatch('as-radio-group', 'handleChange', this.model);
      });
    }
  }
};
</script>



