<template>
  <div class="as-popper" :style="computedStyle">
    <slot></slot>
  </div>
</template>

<script>
import Vue from 'vue';
import { getStyle } from '../../utils/dom';
const isServer = Vue.prototype.$isServer;
const PopperJS = isServer ? function() {} : require('popper.js/dist/umd/popper.js');

export default {
  name: 'as-popper',
  props: {
    placement: {
      type: String,
      default: 'bottom-start'
    },
    fullWidth: Boolean
  },
  data() {
    return {
      popper: null,
      width: '',
      popperVisible: false
    };
  },
  computed: {
    computedStyle() {
      const style = {
        zIndex: '999',
        width: 'max-content'
      };
      if (this.width) {
        style.width = `${this.width}px`;
      }
      return style;
    }
  },
  methods: {
    update() {
      if (isServer) return;
      if (this.popper) {
        this.$nextTick(() => {
          this.popper.update();
          this.popperVisible = true;
        });
      } else {
        this.$nextTick(() => {
          this.popper = new PopperJS(this.$parent.$refs.reference, this.$el, {
            placement: this.placement,
            modifiers: {
              computedStyle: {
                gupAcceleration: false
              },
              preventOverflow: {
                boundariesElement: 'window'
              }
            },
            onCreate: () => {
              this.resetTransformOrigin();
              this.$nextTick(this.popper.update());
            },
            onUpdate: () => {
              this.resetTransformOrigin();
            }
          });
        });
      }
      if (this.fullWidth) {
        console.log('width:', parseInt(getStyle(this.$parent.$el, 'width')));
        this.width = parseInt(getStyle(this.$parent.$el, 'width'));
      }
    },
    destroy() {
      if (this.popper) {
        setTimeout(() => {
          if (this.popper && !this.popperVisible) {
            this.popper.destroy();
            this.popper = null;
          }
          this.popperVisible = false;
        }, 300);
      }
    },
    resetTransformOrigin() {
      if (!this.popper) return;

      const xPlacement = this.popper.popper.getAttribute('x-placement');
      const placementStart = xPlacement.split('-')[0];
      const placementEnd = xPlacement.split('-')[1];
      const leftOrRight = xPlacement === 'left' || xPlacement === 'right';
      if (!leftOrRight) {
        this.popper.popper.style.transformOrigin = placementStart === 'bottom' || (placementStart !== 'top' && placementEnd === 'start') ? 'center top' : 'center bottom';
      }
    }
  },
  created() {
    this.$on('on-update-popper', this.update);
    this.$on('on-destroy-popper', this.destroy);
  },
  beforeDestroy() {
    if (this.popper) {
      this.popper.destroy();
    }
  }
};
</script>



