<template>
  <li :class="computedClass" @click="handleClick">
    <slot></slot>
    <slot name="append"></slot>
  </li>
</template>
<script>
import Emitter from '../../utils/emitter';

export default {
  name: 'as-dropdown-item',
  props: {
    disabled: Boolean,
    divided: Boolean,
    selected: Boolean,
    command: {}
  },
  mixins: [Emitter],
  computed: {
    computedClass() {
      return [
        'as-dropdown-menu__item',
        {
          'as-dropdown-menu__item--disabled': this.disabled,
          'as-dropdown-menu__item--divided': this.divided,
          'as-dropdown-menu__item--selected': this.selected
        }
      ];
    }
  },
  methods: {
    handleClick() {
      if (this.disabled) return;
      this.dispatch('as-dropdown', 'dropdown-menu-item-click', [this.command, this]);
    }
  }
};
</script>



