<template>
  <div :class="[{'as-container': !fluid}, {'as-container--fluid': fluid}, 'clearfix']">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'as-container',
  props: {
    fluid: {
      type: Boolean
    }
  }
};
</script>



