<template>
  <span class="as-cascader__menu-wrapper">
    <ul v-if="data && data.length" class="as-cascader__menu">
      <as-cascader-item
        v-for="(item, index) in data"
        :key="index"
        :data="item"
        :temp-item="tempItem"
        @click.native.stop="handleClickItem(item)"
        @mouseenter.native.stop="handleHoverItem(item)">
      </as-cascader-item>
    </ul>
    <!-- 递归调用自己 -->
    <as-cascader-menu
      v-if="sublist && sublist.length"
      :data="sublist"
      :disabled="disabled"
      :trigger="trigger"
      :change-on-select="changeOnSelect">
    </as-cascader-menu>
  </span>
</template>

<script>
import CascaderItem from './cascader-item.vue';
import Emitter from '../../utils/emitter';
import {findComponentForward, findComponentDownward} from '../../utils/util';

export default {
  name: 'as-cascader-menu',
  mixins: [Emitter],
  props: {
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    disabled: Boolean,
    changeOnSelect: Boolean,
    trigger: String
  },
  data () {
    return {
      tempItem: {},
      result: [],
      sublist: []
    };
  },
  components: {
    'as-cascader-item': CascaderItem
  },
  watch: {
    data() {
      this.sublist = [];
    }
  },
  methods: {
    handleClickItem(item) {
      console.log('handleClickItem');
      if (this.trigger !== 'click' && item.children && item.children.length) return;
      this.handleTriggerItem(item, false, true);
    },
    handleHoverItem(item) {
      if (this.trigger !== 'hover' || !item.children || !item.children.length) return;
      this.handleTriggerItem(item, false, true);
    },
    handleTriggerItem(item, fromInit = false, fromUser = false) {
      if (item.disabled) return;
      console.log('handleTriggerItem');
      if ('loading' in item && !item.children.length) {
        const cascader = findComponentForward(this, 'as-cascader');

        if (cascader && cascader.loadData) {
          cascader.loadData(item, () => {
            if (fromUser) {
              cascader.isLoadChildren = true;
            }
            if (item.children.length) {
              this.handleTriggerItem(item);
            }
          });
          return;
        }
      }

      // 设置临时选中值
      let _backItem = Object.assign({}, item);
      if (_backItem.children) delete _backItem.children;
      const backItem = _backItem;

      if (backItem.label !== this.tempItem.label || backItem.value !== this.tempItem.value) {
        this.tempItem = backItem;
        this.updateItem(backItem);
      }

      if (item.children && item.children.length) {
        this.sublist = item.children;
        this.dispatch('as-cascader', 'on-result-change', {
          lastValue: false,
          changeOnSelect: this.changeOnSelect,
          fromInit: fromInit
        });

        if (this.changeOnSelect) {
          const cascaderMenu = findComponentDownward(this, 'as-cascader-menu');
          if (cascaderMenu) {
            cascaderMenu.$emit('on-clear', true);
          }
        }
      } else {
        this.sublist = [];
        this.dispatch('as-cascader', 'on-result-change', {
          lastValue: true,
          changeOnSelect: this.changeOnSelect,
          fromInit: fromInit
        });
      }
    },
    updateResult(item) {
      this.result = [this.tempItem].concat(item);
      this.updateItem(this.result);
    },
    updateItem(result) {
      if (this.$parent.$options.name === 'as-cascader-menu') {
        this.$parent.updateResult(result);
      } else {
        this.$parent.$parent.updateResult(result);
      }
    }
  },
  mounted() {
    this.$on('on-find-selected', params => {
      let value = [...params.value];
      for (let i = 0; i < value.length; i++) {
        for (let j = 0; j < this.data.length; j++) {
          if (value[i] === this.data[j].value) {
            this.handleTriggerItem(this.data[j], true);
            value.splice(0, 1);
            this.$nextTick(() => {
              this.broadcast('as-cascader-menu', 'on-find-selected', {
                value: value
              });
            });
            return false;
          }
        }
      }
    });

    this.$on('on-clear', (deep = false) => {
      this.sublist = [];
      this.tempItem = {};

      if (deep) {
        const cascaderMenu = findComponentDownward(this, 'as-cascader-menu');
        if (cascaderMenu) {
          cascaderMenu.$emit('on-clear', true);
        }
      }
    });
  }
};
</script>



