<template>
  <label :class="[
    'as-checkbox',
    (border || button) ? 'as-checkbox--' + computedSize : '',
    {
      'as-checkbox--disabled': comptedDisabled,
      'as-checkbox--checked': comptedChecked,
      'as-checkbox--bordered': border,
      'as-checkbox--button': button
    }]"
    role="checkbox" :aria-checked="comptedChecked" :aria-disabled="comptedDisabled">
    <span class="as-checkbox__input">
      <span class="as-checkbox__inner"></span>
      <input type="checkbox" class="as-checkbox__original" aria-hidden="hidden" :value="label" v-model="model" @focus="focus = true" :blur="focus = false" @change="handleChange" :disabled="comptedDisabled" :name="name" tabindex="-1">
    </span>
    <span class="as-checkbox__label">
      <slot></slot>
      <template v-if="!$slots.default">{{label}}</template>
    </span>
  </label>
</template>

<script>
import Emitter from '../../utils/emitter';

export default {
  name: 'as-checkbox',
  inject: {
    asForm: {
      default: ''
    },
    asFormItem: {
      default: ''
    }
  },
  props: {
    value: {},
    label: {},
    disabled: Boolean,
    checked: Boolean,
    name: String,
    border: Boolean,
    button: Boolean,
    size: String
  },
  data () {
    return {
      focus: false,
      selfModel: false
    };
  },
  mixins: [Emitter],
  computed: {
    comptedDisabled() {
      return this.isGroup
        ? this._checkboxGroup.disabled || this.disabled || (this.asForm || {}).disabled
        : this.disabled || (this.asForm || {}).disabled;
    },
    computedSize() {
      const temp = this.size || (this.asFormItem || {}).computedSize;
      return this.isGroup
        ? this._checkboxGroup.computedSize || temp
        : this.size || temp;
    },
    comptedChecked() {
      if ({}.toString.call(this.model) === '[object Boolean]') {
        return this.model;
      } else if (Array.isArray(this.model)) {
        return this.model.indexOf(this.label) > -1;
      }
      return false;
    },
    isGroup() {
      let parent = this.$parent;
      while (parent) {
        if (parent.$options.name !== 'as-checkbox-group') {
          parent = parent.$parent;
        } else {
          /* eslint-disable */
          this._checkboxGroup = parent;
          return true;
        }
      }
      return false;
    },
    store() {
      return this.isGroup ? this._checkboxGroup.value : this.value;
    },
    model: {
      get() {
        return this.isGroup ? this.store : this.value || this.selfModel;
      },
      set(val) {
        if (this.isGroup) {
          this.dispatch('as-checkbox-group', 'input', [val]);
        } else {
          this.$emit('input', val);
          this.selfModel = val;
        }
      }
    }
  },
  methods: {
    handleChange(ev) {
      this.$emit('change', this.model);
      this.$nextTick(() => {
        this.isGroup && this.dispatch('as-checkbox-group', 'change', [this._checkboxGroup.value]);
      });
    }
  },
  watch: {
    value(val) {
      this.dispatch('asFormItem', 'as.form.change', val);
    }
  }
};
</script>



