<template>
  <div :class="computedClass">
    <div class="as-card__header" v-if="$slots.header || header">
      <slot name="header">{{header}}</slot>
    </div>
    <div class="as-card__body" :style="bodyStyle">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'as-card',
  props: {
    header: String,
    shadow: String,
    bodyStyle: {}
  },
  computed: {
    computedClass() {
      return [
        'as-card',
        this.shadow ? `as-card--${this.shadow}-shadow` : 'as-card--always-shadow'
      ];
    }
  }
};
</script>



