<template>
  <button :disabled="computedDisabled || loading" :class="computedClass" type="button" @click="$emit('click')">
    <i :class="['as-icon', 'icon-refresh']" v-if="loading"></i>
    <i :class="['as-icon', 'icon-' + icon]" v-if="icon && !loading"></i>
    <span class="as-button__content" v-if="$slots.default"><slot></slot></span>
  </button>
</template>

<script>
export default {
  name: 'as-button',
  inject: {
    asForm: {
      default: ''
    },
    asFormItem: {
      default: ''
    }
  },
  props: {
    type: {
      type: String,
      default: 'default'
    },
    size: {
      type: String,
      default: 'md',
      validator(val) {
        return ['lg', 'md', 'sm', 'xs'].indexOf(val) > -1;
      }
    },
    icon: String,
    loading: Boolean,
    disabled: Boolean,
    plain: Boolean,
    outline: Boolean,
    round: Boolean,
    circle: Boolean
  },
  computed: {
    computedDisabled() {
      return this.disabled || (this.asForm || {}).disabled;
    },
    computedClass() {
      const {type, size, loading, plain, round, circle, outline} = this;
      return [
        'as-button',
        type ? `as-button--${type}` : '',
        `as-button--${size}`,
        {
          'as-button--disabled': this.computedDisabled,
          'as-button--loading': loading,
          'as-button--plain': plain,
          'as-button--round': round,
          'as-button--circle': circle,
          'as-button--outline': outline
        }
      ];
    }
  }
};
</script>



