<template>
  <div class="as-affix">
    <div v-if="affixed" :style="style"></div>
    <div :style="styles">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AsAffix',
  props: {
    offset: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      affixed: false,
      styles: {}
    };
  },
  computed: {
    style() {
      return {
        width: this.$el.offsetWidth + 'px',
        height: this.$el.offsetHeight + 'px'
      };
    }
  },
  methods: {
    scrolling() {
      const scrollTop = this.getScroll(true);
      const elOffset = this.getOffset(this.$el);
      if (!this.affixed && scrollTop > elOffset.top) {
        this.affixed = true;
        this.styles = {
          top: this.offset + 'px',
          left: elOffset.left + 'px',
          width: this.$el.offsetWidth + 'px',
          position: 'fixed'
        };
      }
      if (this.affixed && scrollTop < elOffset.top) {
        this.affixed = false;
        this.styles = {};
      }
    },
    getScroll(isTop) {
      let res = isTop ? window.pageYOffset : window.pageXOffset;
      const event = isTop ? 'scrollTop' : 'scrollLeft';
      if (typeof res !== 'number') {
        res = document.documentElement[event];
        if (typeof res !== 'number') {
          res = document.body[event];
        }
      }
      return res;
    },
    getOffset(ele) {
      const rect = ele.getBoundingClientRect();
      const body = document.body;
      const clientTop = ele.clientTop || body.clientTop || 0;
      const clientLeft = ele.clientLeft || body.clientLeft || 0;
      const scrollTop = this.getScroll(true);
      const scrollLeft = this.getScroll();
      return {
        top: rect.top + scrollTop - clientTop,
        left: rect.left + scrollLeft - clientLeft
      };
    }
  },
  mounted() {
    window.addEventListener('scroll', this.scrolling);
    window.addEventListener('resize', this.scrolling);
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.scrolling);
    window.removeEventListener('resize', this.scrolling);
  }
};
</script>



