export const getValueByPath = function (object, prop) {
  prop = prop || '';
  const paths = prop.split('.');
  let current = object;
  let result = null;
  for (let i = 0, j = paths.length; i < j; i++) {
    const path = paths[i];
    if (!current) break;

    if (i === j - 1) {
      result = current[path];
      break;
    }
    current = current[path];
  }
  return result;
};

export const findComponentForward = (context, componentName, componentNames) => {
  if (typeof componentName === 'string') {
    componentNames = [componentName];
  } else {
    componentNames = componentName;
  }

  let parent = context.$parent;
  let name = parent.$options.name;
  while (parent && (!name || componentNames.indexOf(name) === -1)) {
    parent = parent.$parent;
    if (parent) {
      name = parent.$options.name;
    }
  }

  return parent;
};

export const findComponentDownward = (context, componentName) => {
  const children = context.$children;
  let child = null;

  console.log('children', children);
  if (children.length) {
    for (const item of children) {
      if (componentName === item.$options.name) {
        child = item;
        break;
      } else {
        child = findComponentDownward(item, componentName);
        if (child) break;
      }
    }
  }

  return child;
};

export const escapeRegexpString = (value = '') => String(value).replace(/[|\\{}()[\]^$+*?.]/g, '\\$&');



