<template>
  <transition name="as-notify-fade">
    <div :class="['as-notify', 'as-notify--' + type, {'as-notify--center': center}]" @mouseenter="clearTimer" @mouseleave="startTimer" @click="handleClick" v-show="visible" role="notify">
      <i :class="['as-icon', 'icon-' + type, 'as-notify__icon']" v-show="iconable && title"></i>
      <div class="as-notify__content">
        <i :class="['as-icon', 'icon-' + type, 'as-notify__inlineicon']" v-if="iconable && !title"></i>
        <span class="as-notify__title" v-if="title">{{title}}</span>
        <p class="as-notify__desc" v-if="message">{{message}}</p>
      </div>
      <i class="as-icon icon-close as-notify__close" v-show="closeable" @click.stop="handleClose"></i>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'as-notify',
  data() {
    return {
      visible: false,
      title: '',
      message: '',
      duration: 2500, // 显示的时间
      type: 'info', // 提示的类型
      closeable: false, // 是否显示关闭按钮
      iconable: true, // 是否显示提示的图标
      onClick: null, // 点击时的回调函数
      onClose: null, // 关闭时的回调函数
      closed: false,
      timer: null,
      center: true // 内容是否居中显示
    };
  },
  watch: {
    closed(val) {
      if (val) {
        this.visible = false;
        this.$el.addEventListener('transitionend', this.destroyElement);
      }
    }
  },
  methods: {
    destroyElement() {
      this.$el.removeEventListener('transitionend', this.destroyElement);
      this.$destroy();
      this.$el.parentNode.removeChild(this.$el);
    },
    handleClick() {
      if (typeof this.onClick === 'function') {
        this.onClick();
      }
    },
    handleClose() {
      this.closed = true;
      if (typeof this.onClose === 'function') {
        this.onClose();
      }
    },
    clearTimer() {
      clearTimeout(this.timer);
    },
    startTimer() {
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          if (!this.closed) {
            this.handleClose();
          }
        }, this.duration);
      }
    }
  },
  mounted () {
    this.startTimer();
  }
};
</script>



