<template>
  <li class="as-select__option" v-show="visible" :data-text="text">
    <button ref="option" :disabled="disabled" v-bind="$attrs" :class="computedClass" type="button" name="button" @click="handleClick" @blur="handleBlur" @mouseover="handleMouseover" @mouseout="handleMouseout">
      <i v-if="select.multiple" class="as-icon icon-checked"></i>
      <span v-html="getText"></span>
    </button>
  </li>
</template>

<script>
export default {
  name: 'as-select-option',
  inheritAttrs: false,
  inject: ['select'],
  props: {
    text: String,
    value: [String, Number],
    disabled: Boolean
  },
  data() {
    return {
      hover: false,
      visible: true,
      getText: null,
      valueInput: ''
    };
  },
  computed: {
    computedDisabled() {
      if (this.select.multiple) {
        if (this.isActive) {
          return false;
        } else {
          return this.select.max === this.select.value.length;
        }
      } else {
        return this.disabled;
      }
    },
    isActive() {
      return this.select.multiple ? this.getValue.indexOf(this.value) !== -1 : this.getValue === this.value;
    },
    computedClass() {
      return [
        'as-select__option__btn',
        {
          'as-select__option__btn--checked': this.isActive,
          'as-select__option__btn--withicon': this.select.multiple,
          'as-select__option__btn--disabled': this.computedDisabled
        }
      ];
    },
    getValue() {
      return this.select.value;
    }
  },
  watch: {
    'select.active': function() {
      this.$nextTick(() => {
        if (this.isActive) {
          this.$emit('update:selected', true);
          this.getText = this.text;
          this.putValue();
        } else {
          this.$emit('update:selected', false);
          this.getText = this.text;
          this.putValue();
        }
      });
    },
    valueInput() {
      if (this.visible) {
        let inputs = this.valueInput.split(',');
        if (inputs[inputs.length - 1] === '') {
          this.getText = this.text;
          return;
        }
        let value = inputs[inputs.length - 1];
        let re = new RegExp(value, 'i');
        if (this.text.toUpperCase().indexOf(value.toUpperCase()) === 0) {
          value = value.charAt(0).toUpperCase() + value.slice(1);
        }
        this.getText = this.text.replace(re, `<span class="search-text">${value}</span>`);
      } else {
        this.getText = this.text;
      }
    }
  },
  methods: {
    handleClick() {
      if (this.computedDisabled) {
        return;
      }
      let text = this.text;
      if (!this.select.multiple) {
        this.select.active = false;
        document.removeEventListener('click', this.select.handleBlur);
        this.select.valuex = text;
        this.select.$emit('input', this.value);
        this.select.changeValue();
      } else if (this.select.multiple) {
        this.select.valuex = text;
        this.select.addMultiple(this.value);
      }
      this.select.$children.map(child => (child.valueInput = ''));
    },
    handleBlur(ev) {
      if (ev.relatedTarget ? !ev.relatedTarget.closest('.as-select__dropdown') : true) {
        this.select.closeOptions();
      }
    },
    handleMouseover(ev) {
      this.$emit('mouseover', ev);
      this.hover = true;
    },
    handleMouseout(ev) {
      this.$emit('mouseout', ev);
      this.hover = false;
    },
    putValue() {
      if (this.value === this.select.value) {
        this.select.valuex = this.text;
      }
    }
  },
  created() {
    this.putValue();
    this.$nextTick(() => {
      if (this.isActive) {
        this.$emit('update:selected', true);
        this.getText = this.text;
        this.putValue();
      } else {
        this.$emit('update:selected', false);
        this.getText = this.text;
        this.putValue();
      }
    });
  },
  updated() {
    this.putValue();
  }
};
</script>



