<template>
  <div :class="computedClass" @mouseover="hover=true" @mouseout="hover=false">
    <template v-if="type!== 'textarea'">
      <div class="as-input-group__prepend" v-if="$slots.prepend">
        <slot name="prepend"></slot>
      </div>
      <input
        :type="type"
        v-bind="$attrs"
        class="as-input__inner"
        :autocomplete="autocomplete"
        :value="currentValue"
        ref="input"
        @input="handleInput"
        @focus="handleFocus"
        @change="handleChange"
        @blur="handleBlur"
        :disabled="computedDisabled"
        :readonly="readonly">

      <!-- prefix -->
      <span class="as-input__prefix" v-if="$slots.prefix || prefixIcon">
        <slot name="prefix"></slot>
        <i v-if="prefixIcon" :class="['as-icon', 'as-input__icon', 'icon-' + prefixIcon]"></i>
      </span>

      <!-- suffix -->
      <span class="as-input__suffix" v-if="$slots.suffix || suffixIcon || showClear || validateState || needState">
        <span class="as-input__suffix-inner">
          <template v-if="!showClear">
            <slot name="suffix"></slot>
            <i v-if="suffixIcon" :class="['as-icon', 'as-input__icon', 'icon-' + suffixIcon]"></i>
          </template>
          <i v-else class="as-icon icon-closed as-input__icon as-input__clear" @click="clear"></i>
        </span>
        <i v-if="validateState" :class="['as-icon', 'as-input__icon', 'icon-' + validateIcon, 'as-input__validateIcon']"></i>
      </span>

      <div class="as-input-group__append" v-if="$slots.append">
        <slot name="append"></slot>
      </div>
    </template>
    <textarea
      v-else
      class="as-textarea__inner"
      ref="textarea"
      :disabled="computedDisabled"
      :readonly="readonly"
      :value="currentValue"
      :style="textareaCalcStyle"
      @input="handleInput"
      @focus="handleFocus"
      @change="handleChange"
      @blur="handleBlur"
      v-bind="$attrs">
    </textarea>
  </div>
</template>

<script>
import calcTextareaHeight from './calcTextareaHeight';
import Emitter from '../../utils/emitter';

export default {
  name: 'as-input',
  inheritAttrs: false,
  mixins: [Emitter],
  inject: {
    asForm: {
      default: ''
    },
    asFormItem: {
      default: ''
    }
  },
  props: {
    value: [Number, String],
    size: String,
    resize: String,
    form: String,
    disabled: Boolean,
    readonly: Boolean,
    autosize: {
      type: [Boolean, Object],
      default: false
    },
    type: {
      type: String,
      default: 'text'
    },
    autocomplete: {
      type: String,
      default: 'off'
    },
    validateEvent: {
      type: Boolean,
      default: true
    },
    suffixIcon: String,
    prefixIcon: String,
    clearable: Boolean
  },
  data() {
    return {
      currentValue: this.value === undefined || this.value === null ? '' : this.value,
      textareaCalcStyle: {},
      hovered: false,
      focused: false
    };
  },
  computed: {
    validateState() {
      return (this.asFormItem && this.asFormItem.validateState) || '';
    },
    needState() {
      return (this.asForm && this.asForm.stateIcon) || false;
    },
    validateIcon() {
      return {
        'validating': 'loading',
        'success': 'success',
        'error': 'close'
      }[this.validateState];
    },
    computedSize() {
      return this.size || (this.asFormItem || {}).computedSize;
    },
    computedDisabled() {
      return this.disabled || (this.asForm || {}).disabled;
    },
    computedClass() {
      return [
        this.type === 'textarea' ? 'as-textarea' : 'as-input',
        this.computedSize ? 'as-input--' + this.computedSize : '',
        {
          'as-input--disabled': this.computedDisabled,
          'as-input-group': this.$slots.prepend || this.$slots.append,
          'as-input-group--prepend': this.$slots.prepend,
          'as-input-group--append': this.$slots.append,
          'as-input--prefix': this.$slots.prefix || this.prefixIcon,
          'as-input--suffix': this.$slots.suffix || this.suffixIcon
        }
      ];
    },
    showClear() {
      return this.clearable && !this.computedDisabled && !this.readonly && this.currentValue !== '' && (this.focused || this.hovered);
    }
  },
  watch: {
    value(val) {
      this.setCurrentValue(val);
    }
  },
  methods: {
    focus() {
      (this.$refs.input || this.$refs.textarea).focus();
    },
    blur() {
      (this.$refs.input || this.$refs.textarea).blur();
    },
    select() {
      (this.$refs.input || this.$refs.textarea).select();
    },
    handleBlur(event) {
      this.focused = false;
      this.$emit('blur', event);
      if (this.validateEvent) {
        this.dispatch('as-form-item', 'as.form.blur', [this.currentValue]);
      }
    },
    handleFocus(event) {
      this.focused = true;
      this.$emit('focus', event);
    },
    handleInput(event) {
      const val = event.target.value;
      this.setCurrentValue(val);
      this.$emit('input', val);
    },
    handleChange(event) {
      this.$emit('change', event.target.value);
    },
    setCurrentValue(val) {
      this.currentValue = val;
      this.$nextTick(this.resizeTextarea);
      if (this.validateEvent && this.currentValue === this.value) {
        this.dispatch('as-form-item', 'as.form.change', [val]);
      }
    },
    resizeTextarea() {
      if (this.$isServer) return;
      const {autosize, type} = this;
      if (type !== 'textarea') return;
      if (!autosize) {
        this.textareaCalcStyle = {
          minHeight: calcTextareaHeight(this.$refs.textarea).minHeight
        };
        return;
      }

      const minRows = autosize.minRows;
      const maxRows = autosize.maxRows;
      this.textareaCalcStyle = calcTextareaHeight(this.$refs.textarea, minRows, maxRows);
    },
    calcIconOffset(place) {
      let elList = this.$el.querySelectorAll(`.as-input__${place}`) || [];
      if (!elList.length) return;

      let el = null;
      for (let i = 0; i < elList.length; i++) {
        if (elList[i].parentNode === this.$el) {
          el = elList[i];
          break;
        }
      }

      if (!el) return;
      const placeMap = {
        'suffix': 'append',
        'prefix': 'prepend'
      };
      const pendant = placeMap[place];
      if (this.$slots[pendant]) {
        el.style.transform = `translateX(${place === 'suffix' ? '-' : ''}${this.$el.querySelector(`.as-input-group__${pendant}`).offsetWidth}px)`;
      } else {
        el.removeAttribute('style');
      }
    },
    updateIconOffset() {
      this.calcIconOffset('suffix');
      this.calcIconOffset('prefix');
    },
    clear() {
      this.$emit('input', '');
      this.$emit('change', '');
      this.$emit('clear', '');
      this.setCurrentValue('');
      this.focus();
    }
  },
  created() {
    this.$on('inputSelect', this.select);
  },
  mounted() {
    this.resizeTextarea();
    this.updateIconOffset();
  },
  updated () {
    this.$nextTick(this.updateIconOffset);
  }
};
</script>



