<template>
  <div class="as-radio__group" role="radioGroup">
    <slot></slot>
  </div>
</template>

<script>
import Emitter from '../../utils/emitter';

export default {
  name: 'as-radio-group',
  inject: {
    asFormItem: {
      default: ''
    }
  },
  props: {
    value: {},
    size: String,
    fill: String,
    disabled: Boolean
  },
  mixins: [Emitter],
  computed: {
    computedSize() {
      return this.size || (this.asFormItem || {}).computedSize;
    }
  },
  created() {
    this.$on('handleChange', value => this.$emit('change', value));
  },
  watch: {
    value(val) {
      this.dispatch('as-form-item', 'as.form.change', [this.value]);
    }
  }
};
</script>



