<template>
  <div :class="computedClass" v-clickoutside="handleClose">
    <div
      class="as-cascader__control"
      ref="reference"
      @click="toggleOpen"
      @mouseover="hover=true"
      @mouseout="hover=false">
      <input type="hidden" :value="currentValue">
      <slot>
        <input
          ref="input"
          class="as-cascader__control-input"
          :readonly="!searchable"
          :disabled="disabled"
          :value="displayedInputValue"
          @input="handleInput"
          :placeholder="displayedPlaceholder"
          type="text">
        <div
          class="as-cascader__control-label"
          v-show="searchable && query === ''"
          @click="handleFocus">
          {{displayedValue}}
        </div>
        <i class="as-icon icon-closed as-cascader__control-arrow" v-show="showCloseIcon" @click.stop="handleClear"></i>
        <i class="as-icon icon-down as-cascader__control-arrow" v-show="!showCloseIcon"></i>
      </slot>
    </div>
    <transition name="dropdown">
      <as-popper
        class="as-cascader__popper"
        ref="popper"
        placement="top-start"
        v-show="visible">
        <div>
          <as-cascader-menu
            v-show="!searchable || (searchable && query === '')"
            ref="cascaderMenu"
            :data="data"
            :disabled="disabled"
            :trigger="trigger"
            :change-on-select="changeOnSelect">
          </as-cascader-menu>
          <div class="as-cascader__search" :style="{width: controllWidth}" v-show="searchable && query !== ''">
            <ul class="as-cascader__search-list" v-show="querySelections.length">
              <li
                :class="['as-cascader__search-item', {'as-cascader__search-item--disabled': item.disabled}]"
                v-for="(item, index) in querySelections"
                :key="index"
                @click="handleSelectItem(index)"
                v-html="item.display">
              </li>
            </ul>
            <span class="as-cascader__search-empty" v-show="!querySelections.length">
              {{searchEmptyText}}
            </span>
          </div>
        </div>
      </as-popper>
    </transition>
  </div>
</template>

<script>
import Emitter from '../../utils/emitter';
import clickoutside from '../../utils/clickoutside';
import CascaderMenu from './cascader-menu.vue';
import Popper from '../../popper';

export default {
  name: 'as-cascader',
  mixins: [Emitter],
  directives: {clickoutside},
  components: {
    'as-cascader-menu': CascaderMenu,
    'as-popper': Popper
  },
  props: {
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    value: {
      type: Array,
      default() {
        return [];
      }
    },
    disabled: Boolean,
    clearable: Boolean,
    searchable: Boolean,
    placeholder: String,
    searchEmptyText: {
      type: String,
      default: 'there is no data'
    },
    size: {
      type: String,
      default: 'md'
    },
    trigger: {
      type: String,
      default: 'click'
    },
    changeOnSelect: Boolean,
    renderFormat: {
      type: Function,
      default(label) {
        return label.join(' / ');
      }
    },
    loadData: Function
  },
  data() {
    return {
      visible: false,
      selected: [],
      tempSelected: [],
      updating: false,
      currentValue: this.value,
      query: '',
      hover: false,
      validDataStr: '',
      isLoadChildren: false,
      left: 0,
      top: 0,
      width: 0,
      controllWidth: 0
    };
  },
  computed: {
    computedClass() {
      return [
        'as-cascader',
        'as-cascader--' + this.size,
        {
          'as-cascader--clearable': this.showCloseIcon,
          'as-cascader--visible': this.visible,
          'as-cascader--disabled': this.disabled,
          'as-cascader--slots': this.$slots.default,
          'as-cascader--empty': this.searchable && this.query !== '' && !this.querySelections.length
        }
      ];
    },
    showCloseIcon() {
      return this.currentValue && this.currentValue.length && this.clearable && !this.disabled && this.hover;
    },
    displayedValue() {
      let label = [];
      for (let i = 0; i < this.selected.length; i++) {
        label.push(this.selected[i].label);
      }

      return this.renderFormat(label, this.selected);
    },
    displayedInputValue() {
      return this.searchable ? this.query : this.displayedValue;
    },
    displayedPlaceholder() {
      return this.searchable && this.currentValue.length ? null : this.placeholder;
    },
    querySelections() {
      let selections = [];
      function getSelections(arrs, label, value) {
        for (let i = 0; i < arrs.length; i++) {
          let item = arrs[i];

          item._label = label ? label + '/' + item.label : item.label;
          item._value = value ? value + ',' + item.value : item.value;

          if (item.children && item.children.length) {
            getSelections(item.children, item._label, item._value);
            delete item._label;
            delete item._value;
          } else {
            selections.push({
              label: item._label,
              value: item._value,
              display: item._label,
              item: item,
              disabled: !!item.disabled
            });
          }
        }
      }

      getSelections(this.data);
      return selections
        .filter(selection => selection.label && selection.label.indexOf(this.query) > -1)
        .map(selection => {
          selection.display = selection.display.replace(new RegExp(this.query, 'g'), `<span>${this.query}</span>`);
          return selection;
        });
    }
  },
  watch: {
    visible (value) {
      if (value) {
        if (this.currentValue.length) {
          this.updateSelected();
        }
        this.broadcast('as-popper', 'on-update-popper');
      } else {
        if (this.searchable) {
          this.query = '';
          this.$refs.input.currentValue = '';
        }
        this.broadcast('as-popper', 'on-destroy-popper');
      }
    },
    value (val) {
      this.currentValue = val;
      if (!val.length) {
        this.selected = [];
      }
    },
    currentValue () {
      this.$emit('input', this.currentValue);
      if (this.updating) {
        this.updating = false;
        return;
      }
      this.updateSelected(true);
    },
    data: {
      deep: true,
      handler () {
        const validDataStr = JSON.stringify(this.getValidData(this.data));
        if (validDataStr !== this.validDataStr) {
          this.validDataStr = validDataStr;
          if (!this.isLoadChildren) {
            this.$nextTick(() => this.updateSelected(false, this.changeOnSelect));
          }
          this.isLoadChildren = false;
        }
      }
    }
  },
  methods: {
    handleClear() {
      if (this.disabled) return;

      const oldValue = JSON.stringify(this.currentValue);
      this.currentValue = this.selected = this.tempSelected = [];
      this.handleClose();
      this.updateValue(this.currentValue, oldValue);
      this.broadcast('as-cascader-menu', 'on-clear');
    },
    handleInput(event) {
      this.query = event.target.value;
    },
    handleSelectItem(index) {
      const item = this.querySelections[index];

      if (item.item.disabled) return;
      this.query = '';
      this.$refs.input.currentValue = '';
      const old = JSON.stringify(this.currentValue);
      this.currentValue = item.value.split(',');
      this.updateValue(this.currentValue, old);
      this.handleClose();
    },
    handleFocus() {
      this.$refs.input.focus();
    },
    toggleOpen() {
      if (this.disabled) return;

      if (this.visible) {
        if (!this.searchable) {
          this.handleClose();
        }
      } else {
        this.visible = true;
        if (!this.currentValue.length) {
          this.broadcast('as-cascader-menu', 'on-clear');
        }
      }
    },
    handleClose() {
      if (this.trigger !== 'click') return false;
      this.visible = false;
    },
    updateResult(result) {
      this.tempSelected = result;
    },
    updateSelected(init = false, changeOnSelect = false) {
      if (!this.changeOnSelect || init || changeOnSelect) {
        this.broadcast('as-cascader-menu', 'on-find-selected', {
          value: this.currentValue
        });
      }
    },
    updateValue(val, old) {
      if (JSON.stringify(val) !== old) {
        this.$emit('change', this.currentValue, JSON.parse(JSON.stringify(this.selected)));
        this.$nextTick(() => {
          this.dispatch('as-form-item', 'as.form.change', {
            value: this.currentValue,
            selected: JSON.parse(JSON.stringify(this.selected))
          });
        });
      }
    },
    // 排除 loading 后的 data，避免重复触发 updateSelect
    getValidData(data) {
      function deleteData(item) {
        const newItem = Object.assign({}, item);
        if ('loading' in newItem) {
          delete newItem.loading;
        }
        if ('_value' in newItem) {
          delete newItem._value;
        }
        if ('_label' in newItem) {
          delete newItem._label;
        }
        if ('children' in newItem && newItem.children.length) {
          newItem.children = newItem.children.map(i => deleteData(i));
        }
        return newItem;
      }
      return data.map(item => deleteData(item));
    },
    changeMenuPosition() {
      const control = this.$refs.control;
      const controlRect = control.getBoundingClientRect();

      this.$nextTick(() => {
        this.top = controlRect.height + 6;
        this.width = this.$refs.dropdown.clientWidth;
        this.controllWidth = controlRect.width + 'px';
      });
    }
  },
  created() {
    this.validDataStr = JSON.stringify(this.getValidData(this.data));
    this.$on('on-result-change', params => {
      const {lastValue, changeOnSelect, fromInit} = params;
      if (lastValue || changeOnSelect) {
        const oldVal = JSON.stringify(this.currentValue);
        this.selected = this.tempSelected;
        let newVal = [];
        this.selected.forEach((item) => {
          newVal.push(item.value);
        });
        if (!fromInit) {
          this.updating = true;
          this.currentValue = newVal;
          this.updateValue(this.currentValue, oldVal);
        }
      }
      if (lastValue && !fromInit) {
        this.handleClose();
      }
    });
  },
  mounted() {
    this.updateSelected(true);
  }
};
</script>



