<template>
  <li :class="computedClass">
    {{data.label}}
    <i class="as-icon icon-right as-cascader__item-arrow" v-if="showArrow"></i>
    <i class="as-icon icon-loading as-cascader__item-loading" v-if="showLoading"></i>
  </li>
</template>

<script>
export default {
  name: 'as-cascader-item',
  props: {
    data: Object,
    tempItem: Object
  },
  computed: {
    computedClass() {
      return [
        'as-cascader__item',
        {
          'as-cascader__item--active': this.tempItem.value === this.data.value,
          'as-cascader__item--disabled': this.data.disabled
        }
      ];
    },
    showArrow() {
      return (this.data.children && this.data.children.length) || ('loading' in this.data && !this.data.loading);
    },
    showLoading() {
      return 'loading' in this.data && this.data.loading;
    }
  }
};
</script>



