<template>
  <div
    class="as-dropdown"
    v-clickoutside="handleClose"
    @mouseenter="handleMouseEnter"
    @mouseleave="handleMouseLeave">
    <div
      class="as-dropdown__reference"
      ref="reference"
      @click="handleClick">
      <slot></slot>
    </div>
    <transition name="transition-drop">
      <Popper
        class="as-dropdown__popper"
        ref="popper"
        v-show="visible"
        :placement="placement"
        :full-width="fullWidth"
        @mouseenter.native="handleMouseEnter"
        @mouseleave.native="handleMouseLeave">
        <slot name="list"></slot>
      </Popper>
    </transition>
  </div>
</template>

<script>
import Popper from '../../popper';
import clickoutside from '../../utils/clickoutside';

export default {
  name: 'as-dropdown',
  components: {
    Popper
  },
  directives: {
    clickoutside
  },
  props: {
    trigger: {
      type: String,
      default: 'hover'
    },
    placement: {
      type: String,
      default: 'bottom-start'
    },
    fullWidth: Boolean
  },
  data() {
    return {
      visible: false,
      timer: null
    };
  },
  watch: {
    visible(value) {
      if (value) {
        this.$refs.popper.update();
      } else {
        this.$refs.popper.destroy();
      }
    }
  },
  methods: {
    handleClick() {
      if (this.trigger !== 'click') return false;
      this.visible = !this.visible;
    },
    handleMouseEnter() {
      if (this.trigger !== 'hover') return false;
      if (this.timer) clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.visible = true;
      }, 250);
    },
    handleMouseLeave() {
      if (this.trigger !== 'hover') return false;
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
          this.visible = false;
        }, 250);
      }
    },
    handleClose() {
      if (this.trigger !== 'click') return false;
      this.visible = false;
    }
  },
  mounted() {
    this.$on('dropdown-menu-item-click', (command, instance) => {
      this.$emit('command', command, instance);
      this.visible = false;
    });
  }
};
</script>



