<template>
  <div :class="['as-switch', {'as-switch--disabled': computedDisabled, 'as-switch--checked': computedChecked, 'as-switch--setcolor': ['#ffffff', '#fff'].includes(inactiveColor) === false}]" role="switch" :aria-checked="computedChecked" :aria-disabled="computedDisabled" @click="handleClick">
    <input type="checkbox" class="as-switch__input" @change="handleChange" ref="input" :id="id" :name="name" :true-value="activeValue" :false-value="inactiveValue" :disabled="computedDisabled">
    <span :class="['as-switch__label', 'as-switch__label--left', {'as-switch__label--active': !computedChecked}]" v-if="activeText">
      <span v-if="activeText" :aria-hidden="computedChecked">{{activeText}}</span>
    </span>
    <span class="as-switch__core" ref="core"></span>
    <span :class="['as-switch__label', 'as-switch__label--right', {'as-switch__label--active': computedChecked}]" v-if="inactiveText">
      <span v-if="inactiveText" :aria-hidden="!computedChecked">{{inactiveText}}</span>
    </span>
  </div>
</template>

<script>
export default {
  name: 'as-switch',
  inject: {
    asForm: {
      default: ''
    }
  },
  props: {
    value: {
      type: [Boolean, String, Number],
      default: false
    },
    disabled: Boolean,
    activeText: {
      type: String,
      default: '开'
    },
    activeValue: {
      type: [Boolean, String, Number],
      default: true
    },
    activeColor: {
      type: String,
      default: '#009688'
    },
    inactiveText: {
      type: String,
      default: '关'
    },
    inactiveValue: {
      type: [Boolean, String, Number],
      default: false
    },
    inactiveColor: {
      type: String,
      default: '#ffffff'
    },
    name: String,
    id: String
  },
  computed: {
    computedChecked() {
      return this.value === this.activeValue;
    },
    computedDisabled() {
      return this.disabled || (this.asForm || {}).disabled;
    }
  },
  watch: {
    computedChecked() {
      this.$refs.input.checked = this.computedChecked;
      if (this.activeColor || this.inactiveColor) {
        this.setBackgroundColor();
      }
    }
  },
  methods: {
    handleClick() {
      !this.computedDisabled && this.handleChange();
    },
    handleChange() {
      const value = !this.computedChecked ? this.activeValue : this.inactiveValue;
      this.$emit('input', value);
      this.$emit('change', value);
      this.$nextTick(() => {
        this.$refs.input.checked = this.computedChecked;
      });
    },
    setBackgroundColor() {
      const color = this.computedChecked ? this.activeColor : this.inactiveColor;
      this.$refs.core.style.borderColor = color === '#ffffff' ? '#eee' : color;
      this.$refs.core.style.backgroundColor = color;
    }
  },
  mounted() {
    this.$refs.input.checked = this.computedChecked;
    if (this.activeColor || this.inactiveColor) {
      this.setBackgroundColor();
    }
  }
};
</script>



