<template>
  <div class="as-checkbox__group" role="checkboxGroup">
    <slot></slot>
  </div>
</template>

<script>
import Emitter from '../../utils/emitter';

export default {
  name: 'as-checkbox-group',
  inject: {
    asFormItem: {
      default: ''
    }
  },
  props: {
    value: {},
    size: String,
    min: Number,
    max: Number,
    disabled: Boolean
  },
  mixins: [Emitter],
  computed: {
    computedSize() {
      return this.size || (this.asFormItem || {}).computedSize;
    }
  },
  watch: {
    value(val) {
      this.dispatch('as-form-item', 'as.form.change', [this.val]);
    }
  }
};
</script>



