<template>
  <div class="aside-bar">
    <div class="aside-bar__scroller">
      <ul class="menu-list">
        <li class="menu-list__item" v-for="(group, index) in menu" :key="index">
          <span :class="['menu-list__label', {'active': group.children && group.children.some(it => it.label === active)}]" @click="goLink(group)">{{group.label}}</span>
          <ul class="submenu-list" v-if="group.children">
            <li class="submenu-list__item" v-for="(item, ind) in group.children" :key="ind">
              <span :class="['submenu-list__label', {'active': item.label === active}]" @click="goLink(item)">{{item.label}}</span>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: 'ui'
    }
  },
  data() {
    const list = this.$menu;
    const type = this.type;
    return {
      active: list[type] && list[type][0].children[0].label,
      menu: list[type]
    };
  },
  methods: {
    goLink(item) {
      if (item.link) {
        this.active = item.label;
        this.$router.push(item.link);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.aside-bar {
  width: 200px;
  margin-left: -15px;
  height: calc(100vh - 60px);
  &__scroller {
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .menu-list {
    overflow: auto;
    &__item {
      display: block;
    }
    &__label {
      display: block;
      font-size: 16px;
      padding: 12px 10px;
      font-weight: 600;
      border-left: 4px solid transparent;
      transition: all .3s ease-in-out;
      &.active {
        border-left-color:#009688;
        background: #f8f8f8;
      }
    }
  }
  .submenu-list {
    margin: 5px 0;
    overflow: auto;
    &__label {
      display: block;
      font-size: 14px;
      text-indent: 16px;
      padding: 10px 10px;
      transition: all .3s ease-in-out;
      &:hover {
        cursor: pointer;
        background: #f6f6f6;
      }
      &.active {
        color:#009688;
      }
    }
  }
}
</style>



