}
.demo-block .demo-block-code pre {
  margin: 0;
  padding: 0;
}
.sh-checkbox {
  color: #444;
  font-weight: 500;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  user-select: none;
}
table {
  width: auto;
  border: none;
}
table thead {
  border-bottom: 2px solid #ddd;
  line-height: 40px;
  text-align: left;
}
